
.hero-container {
    position: relative;
    height: 400px;
    overflow: hidden;
}
.hero img {
    height: 400px;
    object-fit: cover;
}
.USP img {
    display: block;
    height: 70px;
    margin: 20px auto;
} 
.USP {
    background: #FBF8F1;
    font-size: 25px;
    line-height: 25px;
    margin-bottom: 20px;
    padding-bottom: 25px;

}

.hero-title {
    position: absolute;bottom: 0;
    padding: 20px;
    width: 100%;
}
.hero-title h1{
    font-weight: lighter;
    color:white;
    font-size: 50px;
    line-height: 1;
    margin-bottom: 30px;
}
/* reduce the h1 fornt to 25px on mobile */

.hi-image {
    min-width: 50%;
    position: relative;
}
.hi-image:before {
    content: "";
    padding-top: 100%;
    display: block;
}
.hi-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    display: block;
}

.highlights .block div:not(.hi-image) {
    padding: 20px;
}
.highlights .block {
    align-items: center;
}
.highlights h2:not(.SectionHeader__Heading) {
    font-size: 45px;
    color: #4f857c;
    font-weight: 600;
    margin: 30px 0px;
}

.flex-row-reverse {
    flex-direction: row-reverse;
}	
.SectionHeader__Heading {
    font-size: 40px;
    color: #4f857c;
    padding: 20px 8px;
    z-index: 1;
    top: 26px;
    position: relative;
    line-height: 50px;
}

.SectionHeader__Description p {
    padding-left: 35px;
    padding-right: 37px;
    padding-top: 34px;
    font-size: 17px;
    background: #FBF8f1;
    padding-bottom: 38px;
    position: relative;
}

@media (max-width: 768px) {
    .hero-title h1{
        font-size: 25px;
    }
    .hi-image {
        min-width: 100%;
        
    }
}